.main {
  background: #0079BF;
  height: 86vh;
}

.container {
  padding: 20px;
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

@media screen and (max-width: 600px) {
  .main {
    height: 93vh;
  }

  .container {
    justify-content: center;
  }

}